import React, { useState, useEffect } from "react";
import ArticlesList from "../components/ArticlesList"; // agregando enlaces directos de navegacion
import articleContent from "./Article-content";
import NotFoundPage from "./NotFoundPage";
import CommentsList from "../components/CommentsList";
import UpvotesSection from "../components/UpvotesSection";
import AddCommentForm from "../components/AddCommentForm";

const ArticlePage = ({ match }) => {
  const name = match.params.name; // es el parametro que estamos empleando en la URL
  const article = articleContent.find((article) => article.name === name);

  const [articleInfo, setArticleInfo] = useState({
    upvotes: 0,
    comments: [],
  });

  useEffect(() => {
    const fetchData = async () => {
      const result = await fetch(`/api/articles/${name}`);
      const body = await result.json();
      setArticleInfo(body);
      console.log(body);
    };
    fetchData();
  }, [name]);

  // mostrar los demas articulos excepto el ya seleccionado. Por eso es un FILTER
  const otherArticles = articleContent.filter(
    (article) => article.name !== name
  );

  if (!article) return <NotFoundPage />;

  return (
    <React.Fragment>
      <h1>{article.title}</h1>
      <UpvotesSection
        articleName={name}
        upvotes={articleInfo.upvotes}
        setArticleInfo={setArticleInfo}
      />
      {article.content.map((paragraph, ind) => (
        <p key={ind}>{paragraph}</p>
      ))}
      {/*Usamos nuestra variable de estado para asignar los atributos a pasar en "CommentsList"*/}
      <CommentsList comments={articleInfo.comments} />
      <AddCommentForm articleName={name} setArticleInfo={setArticleInfo} />
      <h1 style={{ textAlign: "left", margin: "40px 0px" }}>Otros articulos</h1>
      <ArticlesList articles={otherArticles} />
    </React.Fragment>
  );
};

export default ArticlePage;



