import React from "react";

const CommentsList = ({ comments }) => {
  return (
    <div className="commentSection">
      <h3>Comentarios</h3>
      {comments.map((comment, ind) => {
        const { username, text } = comment;
        return (
          <div className="comment" key={ind}>
            <h4>{username}</h4>
            <p>{text}</p>
          </div>
        );
      })}
    </div>
  );
};

export default CommentsList;



