import React, { useState } from "react";

const AddCommentForm = ({ articleName, setArticleInfo }) => {
  const [username, setUsername] = useState("");
  const [commentText, setCommentText] = useState("");

  /*Enviaremos un JSON que el servidor pueda parsear, por lo que debemos incluir el header
  para reconocimiento del tipo de data enviada en la peticion o FETCH (JSON) */

  async function addComment() {
    const result = await fetch(`/api/articles/${articleName}/add-comment`, {
      method: "post",
      body: JSON.stringify({ username, text: commentText }),
      headers: { "Content-Type": "application/json" },
    });
    const body = await result.json();
    setArticleInfo(body);
    setUsername("");
    setCommentText("");
  }

  return (
    <div id="add-comment-form">
      <h3>Agregar un comentario</h3>
      <label>
        Name:
        <input
          type="text"
          placeholder="Indique su nombre"
          value={username}
          onChange={(e) => setUsername(e.target.value)}
        />
      </label>
      <label>
        Comment:
        <textarea
          placeholder="escriba sus comentarios"
          rows="4"
          cols="50"
          value={commentText}
          onChange={(e) => setCommentText(e.target.value)}
        />
      </label>
      <button onClick={addComment}>Añadir Comentarios</button>
    </div>
  );
};

export default AddCommentForm;



